<template>
  
  <table class="table m-0 table-top-line-none">
    <tr class="text-left" v-for="(v, index) in tags" :key="v.id">
      <td width="10%" class="text-accent">{{ index + 1 }}</td>
      <td width="25%">{{ v.named }}</td>
      <td width="20%">
        <div class="tag-color" :style="{ 'background': v.color }"></div>&nbsp;&nbsp;{{ v.color }}
      </td>
      <td width="30%">{{ ser_timed(v.add_time) }}</td>
      
      <td width="10%" class="text-center"></td>
    </tr>
  </table>

</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

export default {
  name: 'TagViewBody',
  props: {
    tags: {
      type: Array
    }
  },
  methods: {
    ser_timed(v) {
      return tool.timed.ser_timed(v, true, false)
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


