<template>
  <full-height-layout>
    <h3 slot="layout_title">标签</h3>

    <div slot="layout_inner_head">

      <h5 class="fw-none mt-2">
        全部
      </h5>

      <tag-view-top></tag-view-top>

    </div>

    <tag-view-body :tags="tagList" v-if="tagList.length > 0" slot="layout_inner_body"></tag-view-body>

  </full-height-layout>
</template>

<script>
import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import TagViewTop from './Top/TagViewTop.vue'
import TagViewBody from './Body/TagViewBody.vue'

import conn from '@/service/conn'

export default {
  components: { TagViewTop, FullHeightLayout, TagViewBody },
  name: 'TagView',
  data() {
    return {
      tagList: []
    }
  },
  mounted() {

    conn.tag.getList_Tag().then(res => {
      this.tagList = res
    })
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


